import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Header, Button, Group, Cell, Div, Avatar, Text, Link } from '@vkontakte/vkui';

import persik from '../img/persik.png';
import './Persik.css';


const Home = ({ id, go}) => (
	<Panel id={id}>
		<PanelHeader>TimeTable</PanelHeader>
		<img className="Persik" src={persik} alt="Persik The Cat"/>
		<Group header={<Header mode="secondary"> </Header>}>
			<Div>
				<Button stretched size="l" mode="secondary" onClick={go} data-to="persik">
					Посмотреть расписание
				</Button>
				<Link className="jump" href="https://docs.google.com/forms/d/e/1FAIpQLSd8TulV8x1bEWUy3G_noGZ6UmAH_4M9yz1qpod6hL4293VY-g/viewform" target="_blank">Оставить свой отзыв тут</Link>
			</Div>

		</Group>

	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
